.form {
  width: 250px;
  padding: 15px;
  margin-top: 20px;
  background-color: azure;
  border-radius: 30px;
}
.label {
  display: flex;
  flex-direction: column;
  font-weight: 500;
  gap: 10px;
}
.input {
  width: 150px;
  padding: 5px;
  margin-bottom: 15px;
  outline: none;
  border-radius: 2px;
  border: 1px solid gainsboro;
  box-shadow: 1px 1px 1px rgba(240, 239, 239, 0.5);
  cursor: pointer;
}
.btn {
  background-color: white;
  padding: 5px 10px;
  font-weight: 600;
  outline: none;
  border-radius: 2px;
  border: 1px solid gainsboro;
  box-shadow: 1px 1px 1px rgba(240, 239, 239, 0.5);
  cursor: pointer;
}
.btn:hover,
.btn:focus {
  background-color: blue;
  box-shadow: none;
}
